import {connect} from 'react-redux'

import {adProductSelector} from 'data/redux/selectors/productSelector';
import {getLoadProductActionCreator} from 'data/redux/actions/productsActions';
import AcceptDocumentsFromSellerButton
  from 'gui/pages/mainRouter/pages/ad/components/commons/thirdStage/components/ownershipTransferBuyerAcceptance/components/acceptDocumentsFromSellerButton';
import {ORDER_STATUS_APPROVED_TRANSFER} from 'data/redux/constants/ordersConstants';

import {orderSelector} from 'data/redux/selectors/orderSelector';
import {getUpdateOrderActionCreator} from 'data/redux/actions/ordersActions';

const mapStateToProps = (state, ownProps = {}) => {
  return {
    order: orderSelector(adProductSelector(state))(state),
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
    approve: (id) => dispatch(getUpdateOrderActionCreator({
      id,
      status: ORDER_STATUS_APPROVED_TRANSFER
    } as any))
      .then(() => dispatch(getLoadProductActionCreator(id, {updateModification: true}))),
  };
};

const AcceptDocumentsFromSellerButtonContainer = connect(mapStateToProps, mapDispatchToProps)(AcceptDocumentsFromSellerButton as any);

export default AcceptDocumentsFromSellerButtonContainer;
