import {connect} from 'react-redux'

import {
  adProductSelector,
  isProductValidBankSelector,
  productDocumentFilesSelector
} from 'data/redux/selectors/productSelector';
import {PRODUCT_STATUS_ORDER_APPROVED_WAITING_TRANSFER_APPROVAL_BY_BUYER} from 'data/redux/constants/productsConstants';
import SendDocumentsToBuyerButton
  from 'gui/pages/mainRouter/pages/ad/components/commons/thirdStage/components/ownershipTransferSteps/components/sendDocumentsToBuyerButton/SendDocumentsToBuyerButton';
import {getLoadProductActionCreator, getUpdateProductActionCreator} from 'data/redux/actions/productsActions';

const mapStateToProps = (state, ownProps = {}) => {
  const initialFiles = productDocumentFilesSelector(state);
  return {
    product: adProductSelector(state),
    filesAdded: initialFiles && initialFiles.length > 0,
    bankValid: isProductValidBankSelector(state),
    ...ownProps,
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
    approve: (id) => dispatch(getUpdateProductActionCreator({
      id,
      status: PRODUCT_STATUS_ORDER_APPROVED_WAITING_TRANSFER_APPROVAL_BY_BUYER
    } as any)).then(() => dispatch(getLoadProductActionCreator(id))),
  };
};

const SendDocumentsToBuyerButtonContainer = connect(mapStateToProps, mapDispatchToProps)(SendDocumentsToBuyerButton as any) as any;

export default SendDocumentsToBuyerButtonContainer;
