import {connect} from 'react-redux'
import {getQueryAutocompleteBanksActionCreator, IPredictionFormatted} from 'data/redux/actions/guiActions';
import BranchSelector
  from 'gui/pages/mainRouter/pages/ad/components/commons/thirdStage/components/ownershipTransferSteps/components/bankDetails/components/bankDetailsForm/components/branchSelector';
import {getBranchesSelector} from 'data/redux/selectors/guiSelector';

const mapStateToProps = (state, {label, field}) => {
  return {
    branches: getBranchesSelector(state) || [],
    label,
    field
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
    queryBranches: (query: string, bankCode?: number): Promise<IPredictionFormatted[]> => dispatch(getQueryAutocompleteBanksActionCreator(query, {
      bank: false,
      bankCode
    }))
      .then((predictions: any[]) => predictions.map((branch) => ({label: branch.branchName, value: branch}))),
  };
};

const AutoCompleteBranchSelectorContainer = connect(mapStateToProps, mapDispatchToProps)(BranchSelector);

export default AutoCompleteBranchSelectorContainer;
