import {connect} from 'react-redux'
import {getQueryAutocompleteBanksActionCreator} from 'data/redux/actions/guiActions';
import BankSelector
  from 'gui/pages/mainRouter/pages/ad/components/commons/thirdStage/components/ownershipTransferSteps/components/bankDetails/components/bankDetailsForm/components/bankSelector';
import {getBanksSelector} from 'data/redux/selectors/guiSelector';
import {IPredictionFormatted} from 'data/interfaces/products';

const mapStateToProps = (state, {label, field}) => {
  return {
    banks: getBanksSelector(state) || [],
    label,
    field,
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
    queryBanks: (query: string): Promise<IPredictionFormatted[]> => dispatch(getQueryAutocompleteBanksActionCreator(query, {bank: true}))
      .then((predictions: any[]) => predictions.map((bank) => ({label: bank.bankName, value: bank}))),
  };
};

const AutoCompleteBankSelectorContainer = connect(mapStateToProps, mapDispatchToProps)(BankSelector);

export default AutoCompleteBankSelectorContainer;
