import React, {useEffect} from "react";
import styles from './BranchSelector.module.scss';
import FormikSearchSelector from "gui/components/formikSearchSelector/FormikSearchSelector";
import {Grid} from '@material-ui/core';
import {components} from 'react-select';

import {IBankQueryItem} from 'data/redux/reducers/guiReducer';

export interface IBankSelectorProps {
  label: string;
  field?: any;
  queryBranches: (query, bankCode?: any) => Promise<any>;
  branches: IBankQueryItem[];
  form?: any;
}

const NoOptionsMessage = () => {
  return (
    <p>
      חפש/י לפי שם או מספר סניף
    </p>
  );
};

const formatBankLabel = ({
                           branchCode,
                           branchName,
                           bankName
                         }): string => branchCode && branchName && bankName && `(${branchCode})${branchName} ${bankName}`;

const Option = ({children, ...restProps}) => {
  return (
    <components.Option {...restProps}>
      <Grid container>
        <Grid item>
          {formatBankLabel(restProps.data.value)}
        </Grid>
      </Grid>
    </components.Option>
  );
};


const BranchSelector = (props: IBankSelectorProps) => {
  const bankCode = props.form.values.bank && props.form.values.bank.bankCode;
  useEffect(() => {
    if (bankCode > 0) {
      props.queryBranches('', bankCode);
    }
  }, [props.field.value, bankCode]);
  return (
    <FormikSearchSelector
      className={styles.branchSelectorContainer}
      options={props.branches
        .filter(branch => branch)
        .map((branch) => ({label: formatBankLabel(branch), value: branch})) as any}
      onChange={((value) => {
        props.field.onChange({
          target: {
            name: props.field.name,
            value: value,
          },
        });
      })}
      components={{Option, NoOptionsMessage}}
      promiseOptions={(query) => props.queryBranches(query, bankCode)}
      onFocus={() => {
        if (bankCode > 0) {
          props.queryBranches('', bankCode);
        }
      }}
      {...props}
    />
  );
};

BranchSelector.defaultProps = {
  queryBranches: () => Promise.resolve(),
};

export default BranchSelector;
