import {connect} from 'react-redux'

import {adPageUrlSelector, adProductSelector, productDocumentFilesSelector} from 'data/redux/selectors/productSelector';
import {orderSelector} from 'data/redux/selectors/orderSelector';
import UploadDocuments
  from 'gui/pages/mainRouter/pages/ad/components/commons/thirdStage/components/ownershipTransferSteps/components/uploadDocuments';
import {
  getCreateDocumentWithFilesForProduct,
  getDeleteFileFromDocumentForProduct,
  getLoadInitialFiles
} from 'data/redux/actions/documentsActions';

const mapStateToProps = (state, ownProps) => {
  const product = adProductSelector(state);
  const {id, ...restParsedUrl}: any = adPageUrlSelector(state);
  const initialFiles = productDocumentFilesSelector(state);
  return {
    product: product,
    order: orderSelector(product)(state),
    initialFiles: initialFiles
      .map(uploadedFile => {
        const file = new File([""], uploadedFile.name);
        (file as any).uploadedFile = {
          id: uploadedFile.id
        };
        return file;
      }),
    filesAdded: initialFiles && initialFiles.length > 0,
    ...ownProps
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
    onFilesAdd: (product_id, files) => dispatch(getCreateDocumentWithFilesForProduct(product_id, files)),
    onFileDelete: (product_id, file) => dispatch(getDeleteFileFromDocumentForProduct(product_id, file)),
    loadInitialFiles: (product_id) => dispatch(getLoadInitialFiles(product_id)),
  };
};

const UploadDocumentsContainer = connect(mapStateToProps, mapDispatchToProps)(UploadDocuments);

export default UploadDocumentsContainer;
