import React, {useState} from "react";
import styles from './UploadDocuments.module.scss';
import {IProduct} from 'data/interfaces/products';
import {IOrder} from 'data/interfaces/orders';
import {DropzoneArea} from 'material-ui-dropzone';
import UploadDocumentContainer from "data/redux/containers/uploadDocumentContainer/UploadDocumentContainer";
import Step from '../step';
import {Checkbox, FormControlLabel} from '@material-ui/core';
import {
  hotelDetails,
  identifyProductCategoryType,
  ticketDetails,
  vacationDetails,
  voucherDetails
} from 'data/redux/constants/productsConstants';

export interface IPassengersProps {
  order: IOrder
  product: IProduct;

  onFilesAdd: (product_id: number, files: File[]) => void;
  onFileDelete: (product_id: number, file: File) => void;
  loadInitialFiles: (product_id: number) => void;
  initialFiles: any;
  onStateChange?: (complete: boolean) => void;
  filesAdded?: boolean;
}

const stepText = (props: IPassengersProps) => {
  switch (identifyProductCategoryType(props.product)) {
    case ticketDetails:
      return 'לאחר שינוי השם וקבלת המסמכים מספק הנסיעות, אנא וודא כי השמות החדשים אכן רשומים ללא שגיאות כתיב והעלה אותם לאתר.';
    case hotelDetails:
      return 'לאחר שינוי השם וקבלת המסמכים מספק הנסיעות, אנא וודא כי השמות החדשים אכן רשומים ללא שגיאות כתיב והעלה אותם לאתר.';
    case vacationDetails:
      return 'לאחר שינוי השם וקבלת המסמכים מספק הנסיעות, אנא וודא כי השמות החדשים אכן רשומים ללא שגיאות כתיב והעלה אותם לאתר.';
    case voucherDetails:
      return 'העלה את המסמכים הנדרשים (שובר). וודא כי קוד השובר מועבר בצורה ברורה.';
  }
  return (null);
}


const UploadDocuments = (props: IPassengersProps) => {
  React.useEffect(() => {
    props.loadInitialFiles(props.product.id);
  }, [props.product.id, props.loadInitialFiles]);
  const [isChangedNames, setAcceptChangedNames] = useState(false);
  return (
    <Step index={2}
          title="קבל את המסמכים או הכרטיסים עם השמות המעודכנים מספק התיירות"
          onStateChange={props.onStateChange}
          errors={{
            'יש לצרף קבצים כדי להמשיך': () => !props.filesAdded,
            'יש לסמן אישור החלפת שמות במסמכים': () => !isChangedNames,
          }}
    >
      <DropzoneArea
        key={`${props.initialFiles.length}-${JSON.stringify(props.initialFiles).length}`}
        initialFiles={props.initialFiles}
        acceptedFiles={[
          'image/*',
          'image/png',
          'image/jpg',
          'image/jpeg',
          'application/msword',
          'application/vnd.openxmlformats-officedocument.wordprocessingml.document',
          'application/vnd.ms-excel',
          'application/vnd.openxmlformats-officedocument.spreadsheetml.sheet',
          'application/vnd.ms-powerpoint',
          'application/vnd.openxmlformats-officedocument.presentationml.presentation',
          'application/pdf',
          'text/html',
          'text/calendar',
          'text/*',
          'application/json',
        ]}
        dropzoneClass={styles.uploadDocumentsContainer}
        filesLimit={10}
        maxFileSize={10000000}
        onDrop={(files, e: any) => {
          props.onFilesAdd(props.product.id, files.filter(file => file.size > 0));
          /** When same file is chosen the input does not come up because the input already has the file chosen.
           * I tried to fix it but its not that important,  very side case.
           setTimeout(() => {
            console.log(312312, e.nativeEvent.target);
            e.nativeEvent.target.outerHTML = e.nativeEvent.target.outerHTML;
          }, 500);
           **/
        }}
        dropzoneText={"לחץ או גרור מסמכים לפה כדי להעלותם"}
        getPreviewIcon={(fileObject, classes: any) => <UploadDocumentContainer fileObject={fileObject}
                                                                               classes={classes}/>}
        onDelete={file => props.onFileDelete(props.product.id, file)}
      />
      <FormControlLabel
        control={<Checkbox color="primary" onClick={event => setAcceptChangedNames((event.target as any).checked)}/>}
        label="אני מאשר שהחלפתי שמות והעלתי את כל המסמכים הרלוונטיים"
      />
    </Step>
  );
};

UploadDocuments.defaultProps =
  {
    product: {}
    ,
    order: {}
    ,
    onFilesAdd: (product_id, files) => {
      
    },

    onFileDelete: (product_id, file) => {
      
    },
    onStateChange: (complete) => {
    },
  };

export default React.memo(UploadDocuments);
