import React from "react";
import styles from './UploadDocument.module.scss';
import {FileObject} from 'material-ui-dropzone';
import {Paper} from "@material-ui/core";

export interface IUploadDocument {
  fileObject: FileObject;
  classes: any;
  thumbnail?: string;
  loading?: boolean;
  loadFileImage: (file_id: string) => Promise<any>;
  downloadFile: (file_id: string) => Promise<any>;

}

const UploadDocument = (props: IUploadDocument) => {
  const fileId = ((props.fileObject.file as any).uploadedFile || {}).id;
  React.useEffect(() => {
    if (!props.thumbnail) {
      props.loadFileImage(fileId)
        .then(s => {
        })
        .catch(e => {
        });
    }
  }, [fileId, props.thumbnail, props.loadFileImage]);
  return (
    <Paper title={props.fileObject.file.name}
           className={`${styles.uploadDocumentContainer} uploadDocumentContainer`}
           onClick={(e) => {
             e.nativeEvent.stopImmediatePropagation();
             e.stopPropagation();
             if (!props.loading) {
               props.downloadFile(fileId);
             }
           }}
    >
      <small>{props.fileObject.file.name.length > 7 ? `${props.fileObject.file.name.slice(0, 7)}...` : props.fileObject.file.name}</small>
      <img width={128}
           height={128}
           src={(!props.loading && props.thumbnail) || '/img/loader.svg'}
           loading="lazy"
      />
    </Paper>
  );
};

UploadDocument.defaultProps = {
  fileObject: {},
  classes: {},
};

export default React.memo(UploadDocument);
