import {connect} from 'react-redux'

import {
  adPageUrlSelector,
  adProductSelector,
  fileIsLoadingSelector,
  fileThumbnailSelector
} from 'data/redux/selectors/productSelector';
import UploadDocument
  from 'gui/pages/mainRouter/pages/ad/components/commons/thirdStage/components/ownershipTransferSteps/components/uploadDocuments/components/UploadedDocument/UploadDocument';
import {getLoadFileDownload, getLoadFileThumbnail} from 'data/redux/actions/documentsActions';

const mapStateToProps = (state, ownProps) => {
  const product = adProductSelector(state);
  const {id, ...restParsedUrl}: any = adPageUrlSelector(state);
  return {
    thumbnail: (ownProps.fileObject.file.uploadedFile && fileThumbnailSelector(ownProps.fileObject.file.uploadedFile.id)(state)) || "",
    loading: (ownProps.fileObject.file.uploadedFile && fileIsLoadingSelector(ownProps.fileObject.file.uploadedFile.id)(state)) || "",
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
    loadFileImage: (file_id) => dispatch(getLoadFileThumbnail(file_id)),
    downloadFile: (file_id) => dispatch(getLoadFileDownload(file_id)),
  };
};

const UploadDocumentContainer = connect(mapStateToProps, mapDispatchToProps)(UploadDocument);

export default UploadDocumentContainer;
