import React, {useEffect} from "react";
import styles from './BankSelector.module.scss';
import FormikSearchSelector from "gui/components/formikSearchSelector/FormikSearchSelector";
import {Grid} from '@material-ui/core';
import {components} from 'react-select';
import {IBankQueryItem} from 'data/redux/reducers/guiReducer';

export interface IBankSelectorProps {
  label: string;
  field?: any;
  queryBanks: (query) => Promise<any>;
  form?: any;
  banks: IBankQueryItem[];
}

const NoOptionsMessage = () => {
  return (
    <p>
      חפש/י לפי שם או מספר בנק
    </p>
  );
};

const formatBankLabel = ({bankCode, bankName}): string => `(${bankCode})${bankName}`;

const Option = ({children, ...restProps}) => {
  return (
    <components.Option {...restProps}>
      <Grid container>
        <Grid item>
          {formatBankLabel(restProps.data.value)}
        </Grid>
      </Grid>
    </components.Option>
  );
};


const useEffectOnce = (cb, ...condition) => {
  const isCalledRef = React.useRef(false);
  return React.useEffect(() => {
    if (condition && !isCalledRef.current) {
      isCalledRef.current = true;
      cb();
    }
  }, [cb, ...condition]);
}

const BankSelector = (props: IBankSelectorProps) => {
  useEffect(() => {
    const branch = props.form.values.branch || {...props.form.values.value};
    if ((props.field.value ? branch.bankCode !== props.field.value.bankCode : true) && branch && branch.bankCode) {
      props.field.onChange({
        target: {
          name: props.field.name,
          value: branch,
        },
      });
    } else if (props.banks.length > 0 && props.field.value && !props.banks.includes(props.field.value)) {
      const bank = props.banks.find((bank) => bank.bankCode === props.field.value.bankCode);
      props.field.onChange({
        target: {
          name: props.field.name,
          value: bank,
        },
      });
    }
    /*
    else if (props.banks.length <= 0) {
      console.log(8888,props.banks);
      props.queryBanks("");
    }

     */
  }, [props.form.values.branch, props.field.value, props.queryBanks, props.banks]);


  useEffectOnce(() => {
    if (props.banks.length <= 0) {
      console.log(8888, props.banks);
      props.queryBanks("");
    }
  }, props.queryBanks, props.banks);

  return (
    <FormikSearchSelector
      className={styles.bankSelectorContainer}
      options={props.banks
        .filter(bank => bank)
        .map((bank) => ({label: formatBankLabel(bank), value: bank})) as any}
      onChange={(async (value) => {
        value = value || undefined;
        if (!props.form.values.branch || props.form.values.branch.bankCode !== (value || {}).bankCode) {
          await props.form.setFieldValue("branch", {
            bankCode: '',
            branchCode: '',
            branchName: '',
            bankName: '',
          }, false);
        }
        if (props.form.values.account) {
          await props.form.setFieldValue("account", "", false);
        }
        await props.form.setFieldValue(props.field.name, value || undefined, false);

      })}
        components={{Option, NoOptionsMessage}}
      {...props}
        />
        );
      };

BankSelector.defaultProps =
{
  queryBanks: () => Promise.resolve(),
};

export default BankSelector;
