import React from "react";
import Step from '../step';
import BankDetailsFormContainer from 'data/redux/containers/bankDetailsFormContainer';

export interface IPassengersProps {
  onStateChange?: (complete: boolean) => void;
  bankValid?: boolean;
}

const BankDetails = (props: IPassengersProps) => {
  return (
    <Step index={3}
          title="פרטי חשבון בנק."
          onStateChange={props.onStateChange}
          errors={{
            'יש להזין פרטי בנק ולשמור': () => !props.bankValid
          }}
    >
      <p>לחשבון זה יועבר התשלום שיתקבל מהקונה</p>
      <p>אנא וודה שפרטי חשבון הבנק אליו תרצה לקבל את הכסף תקינים</p>
      <BankDetailsFormContainer/>
    </Step>
  );
};

BankDetails.defaultProps = {
  onStateChange: (complete) => {
  },
};

export default React.memo(BankDetails);
