import React from 'react';
import {IProduct} from 'data/interfaces/products';
import {IOrder} from 'data/interfaces/orders';
import OwnershipTransferBuyerAcceptanceContainer from 'data/redux/containers/ownershipTransferBuyerAcceptanceContainer';
import OwnershipTransferStepsContainer from 'data/redux/containers/ownershipTransferStepsContainer';
import ThankYouCard from 'gui/pages/mainRouter/pages/ad/components/commons/thirdStage/components/thankYouCard';

interface IThirdStageProps {
  order?: IOrder;
  product?: IProduct;
  isOrderOwner?: boolean;
  isProductOwner?: boolean;
  topChildren?: any;
  bottomChildren?: any;

}

const minHeight = {
  minHeight: "300px",
};


const ThirdStage = (props: IThirdStageProps) => (
  <>
    {props.topChildren}
    <ThankYouCard style={minHeight} {...props}/>
    <OwnershipTransferBuyerAcceptanceContainer/>
    <OwnershipTransferStepsContainer/>
    {props.bottomChildren}
  </>
);

ThirdStage.defaultProps = {
  topChildren: null,
  bottomChildren: null,
};

export default React.memo(ThirdStage);
