import {connect} from 'react-redux'
import {adProductBankDetailsSelector, adProductSelector} from 'data/redux/selectors/productSelector';
import {getUpdateProductActionCreator} from 'data/redux/actions/productsActions';
import {IProduct} from 'data/interfaces/products';
import BankDetailsForm
  from 'gui/pages/mainRouter/pages/ad/components/commons/thirdStage/components/ownershipTransferSteps/components/bankDetails/components/bankDetailsForm';


const mapStateToProps = (state, ownProps) => {
  return {
    product: adProductSelector(state),
    bankDetails: adProductBankDetailsSelector(state),
    ...ownProps,
  };
};


const mapDispatchToProps = (dispatch, ownProps) => {
  return {
    onSubmit: (product: IProduct) => dispatch(getUpdateProductActionCreator(product))
      .catch(e => {
        
        return Promise.resolve({error: true});
      }),
    ...ownProps
  };
};


const BankDetailsFormContainer = connect(mapStateToProps, mapDispatchToProps)(BankDetailsForm);

export default BankDetailsFormContainer;
