import React, {useState} from "react";
import styles from './Step.module.scss';
import InfoCard from "../../../../../infoCard/InfoCard";
import OneIcon from 'gui/components/icons/OneIcon';
import TwoIcon from 'gui/components/icons/TwoIcon';
import ThreeIcon from 'gui/components/icons/ThreeIcon';
import FourIcon from 'gui/components/icons/FourIcon';
import CheckRoundedIcon from '@material-ui/icons/CheckRounded';
import DoneButton from './components/doneButton/DoneButton';
import Collapse from '@material-ui/core/Collapse';
import LoaderContainer from 'data/redux/containers/LoaderContainer';
import {Alert} from '@material-ui/lab';


export interface IStepProps {
  index: number;
  title: string;
  children?: any;
  onStateChange?: (complete: boolean) => void;

  errors?: any,
}

const indexToIcon = index => {
  switch (index) {
    case 1:
      return OneIcon;
    case 2:
      return TwoIcon;
    case 3:
      return ThreeIcon;
    case 4:
      return FourIcon;
    default:
      return OneIcon;
  }
}


const Step = (props: IStepProps) => {
  const [active, _setActiveState] = useState(false);
  const [errorsShown, _setErrorsShownObject] = useState({});
  /**
   * Set error initial state
   * @param error
   * @param state
   */
  const setErrorShownState = (error, state) => {
    _setErrorsShownObject({
      ...errorsShown,
      [error]: state,

    });
  };
  /**
   * Set all errors to initial state
   * @param state
   */
  const setErrorsShownState = (state) => {
    const obj = {...errorsShown};
    Object.keys(props.errors)
      .forEach(error => obj[error] = state);
    _setErrorsShownObject(obj);
  };
  const setActiveState = (state) => {
    props.onStateChange(state);
    _setActiveState(state);
  };
  /**
   * Are there any errors? perform all checks
   */
  const errors = Object.keys(props.errors || {})
    .filter(error => props.errors[error]());
  return (
    <InfoCard
      className={styles.stepContainer}
      subTitleElement={<h5 className={styles.h5}>
        {props.title}
      </h5>}
      icon={active ? CheckRoundedIcon : indexToIcon(props.index)}
      toolTip={['שלב', props.index].join(" ")}
      style={{padding: 0}}
      defaultExpanded={true}
    >
      {
        errors
          .filter(error => errorsShown[error] === true)
          .map(error => <Alert key={error}
                               onClose={() => {
                                 setErrorShownState(error, false);
                               }} severity="error"
                               className="stepErrorMessage"
          >
            {error}
          </Alert>)
      }
      <Collapse in={!active} className={active ? styles.activeCollapse : ''}
                collapsedHeight={200}>
        {props.children}
      </Collapse>
      <DoneButton onClick={() => errors.length > 0 ? setErrorsShownState(true) : setActiveState(!active)}
                  active={active}/>
    </InfoCard>
  );
};

Step.defaultProps =
  {
    index: 1,
    title: "",
    children: null,
    onStateChange: (complete) => {
    },
    errors: {},
  };

export default React.memo(Step);
