import React from "react";
import styles from './AccountInput.module.scss';
import {TextField} from 'formik-material-ui';
import NumberFormat from "react-number-format";
import {Field} from 'formik';

export interface IPriceInputProps {
  name: string;
  label: string | any;
}


interface NumberFormatCustomProps {
  inputRef: (instance: NumberFormat | null) => void;
  onChange: (event: { target: { name: string; value: string } }) => void;
  name: string;
}

const NumberFormatCustom = (props: NumberFormatCustomProps, a) => {
  const {inputRef, onChange, ...other} = props;
  return (
    <NumberFormat
      {...other}
      style={{direction: 'ltr'}}
      getInputRef={inputRef}
      onValueChange={(values) => {
        onChange({
          target: {
            name: props.name,
            //value: values.value.startsWith("0") ? values.value : "0",
            value: values.value
          },
        });
      }}
      allowLeadingZeros={false}
      allowNegative={false}
      isNumericString
      format={`##########`}
      mask="_"
      allowEmptyFormatting
    />
  );
}

const CustomizedTextField = (props: any) => {
  const touched = props.form.touched[props.field.name];
  const error = touched && props.form.errors[props.field.name];
  const valid = touched && !error;
  return (
    <TextField

      className={`${styles.accountInputContainer} ${valid ? "valid" : ""}`.trim()}
      {...props}
    />
  );
}

const AccountInput = (props: IPriceInputProps) => (
  <Field
    component={CustomizedTextField}
    InputProps={{
      inputComponent: NumberFormatCustom as any,
    }}
    required
    fullWidth
    variant="outlined"
    id={props.name}
    {...props}
  />
);

AccountInput.defaultProps = {
  name: "",
  label: "",
};

export default AccountInput;
