import {connect} from 'react-redux'

import {adProductSelector} from 'data/redux/selectors/productSelector';
import {getLoadProductActionCreator} from 'data/redux/actions/productsActions';
import {ORDER_STATUS_ORDER_DISPUTE} from 'data/redux/constants/ordersConstants';

import {orderSelector} from 'data/redux/selectors/orderSelector';
import {getUpdateOrderActionCreator} from 'data/redux/actions/ordersActions';
import ReportProblemWithDocumentsFromSellerButton
  from 'gui/pages/mainRouter/pages/ad/components/commons/thirdStage/components/ownershipTransferBuyerAcceptance/components/reportProblemWithDocumentsFromSellerButton/ReportProblemWithDocumentsFromSellerButton';

const mapStateToProps = (state, ownProps = {}) => {
  return {
    order: orderSelector(adProductSelector(state))(state),
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
    disapprove: (id) => dispatch(getUpdateOrderActionCreator({
      id,
      status: ORDER_STATUS_ORDER_DISPUTE
    } as any))
      .then(() => dispatch(getLoadProductActionCreator(id, {updateModification: true}))),
  };
};

const ReportProblemWithDocumentsFromSellerButtonContainer = connect(mapStateToProps, mapDispatchToProps)(ReportProblemWithDocumentsFromSellerButton as any);

export default ReportProblemWithDocumentsFromSellerButtonContainer;
