import React from "react";
import ErrorOutlineIcon from '@material-ui/icons/ErrorOutline';
import LinkBlockContainer from 'data/redux/containers/linkBlockContainer';
import {linksMap} from 'data/utils/constants';
import InfoCard from 'gui/pages/mainRouter/pages/ad/components/commons/infoCard';
import {IOrder} from 'data/interfaces/orders';
import {Case, Switch, When} from "react-if";
import {
  ORDER_STATUS_APPROVED_TRANSFER,
  ORDER_STATUS_DISAPPROVED,
  ORDER_STATUS_ORDER_APPROVED_WAITING_TRANSFER_APPROVAL_BY_BUYER,
  ORDER_STATUS_ORDER_DISPUTE,
  ORDER_STATUS_ORDER_SOLD,
  ORDER_STATUS_WAITING_FOR_APPROVAL
} from 'data/redux/constants/ordersConstants';
import {
  PRODUCT_STATUS_ORDER_APPROVED_TRANSFERRED,
  PRODUCT_STATUS_ORDER_APPROVED_WAITING_PAYMENT,
  PRODUCT_STATUS_ORDER_APPROVED_WAITING_TRANSFER_APPROVAL_BY_BUYER,
  PRODUCT_STATUS_ORDER_DISPUTE
} from 'data/redux/constants/productsConstants';
import {openCrispChat} from 'gui/components/crispChat';

export interface IThankYouCardProps {
  order: IOrder;
  style?: any;
  isOrderOwner?: boolean;
  isProductOwner?: boolean;
}

const ThankYouCard = (props: IThankYouCardProps) => (<>
    <When condition={props.isOrderOwner}>{() =>
      <Switch>
        <Case condition={props.order.status === ORDER_STATUS_WAITING_FOR_APPROVAL}>
          {() => <InfoCard
            title={
              <b style={{fontSize: 22}}>הזמנתך בוצעה בהצלחה</b>
            }
            icon={ErrorOutlineIcon}
            toolTip="הזמנתך בוצעה בהצלחה"
            style={props.style}
            defaultExpanded={true}
          >
            הזמנתך בוצעה בהצלחה!
            <br/>
            מספר הזמנה
            <span style={{
              fontWeight: 700,
            }}> {props.order.orderIdentifier} </span>
            <br/>
            בקשתך נשלחה למוכר וממתינה לאישור סופי והעלאת הכרטיסים / שוברים למערכת, יש למוכר
            <br/>
            עד 48 שעות לבצע את החלפת השם בשוברים ולהעלות למערכת עם הפרטים שמסרת.
            <br/>
            <br/>
            ברגע שהשוברים יעלו למערכת עם שמך נשלח לך הודעה למייל וב-SMS.
            <br/>
            ניתן להתעדכן בכל עת בסטטוס ההזמנה באיזור האישי.
            <br/>
            <br/>
            <br/>
            יש לכם שאלות?
            {' '}
            <span
              style={{display: "inline", padding: 0, fontWeight: 600, cursor: "pointer"}}
              onClick={() => openCrispChat()}
            >
              אנחנו פה
            </span>
          </InfoCard>}
        </Case>
        <Case condition={props.order.status === ORDER_STATUS_ORDER_APPROVED_WAITING_TRANSFER_APPROVAL_BY_BUYER}>
          {() => <InfoCard
            title={
              <b style={{fontSize: 22}}>הזמנתך בוצעה בהצלחה</b>
            }
            icon={ErrorOutlineIcon}
            toolTip="הועלו מסמכים לטובת הצעתך"
            style={props.style}
            defaultExpanded={true}
          >
            שים לב, הועלו מסמכים לטובת הצעתך מספר
            <span style={{
              color: '#d47474',
              fontWeight: 700,
              fontSize: 18
            }}> {props.order.orderIdentifier} </span>.
            <br/>
            אנא בדוק את המסמכים שאכן מתאימים לשמות בהזמנה שלך.
            <br/>
            בדוק מול חברת התעופה/מלון/סוכן נסיעות שאכן ההזמנה הועברה על שימך.
            <br/>
            אחרי ווידוי שלך שאכן הכל בסדר, אנא אשר את קבלת המסמכים
            <br/>
            חשוב לציין כי הכסף יועבר בקרוב למוכר.
            <br/>
            אנא עשה את הבדיקות הנדרשות בימים הקרובים על מנת שלא נעביר את הכסף למוכר בטרם אישורך שהמסמכים הועברו כשורה.
          </InfoCard>}
        </Case>
        <Case condition={[ORDER_STATUS_APPROVED_TRANSFER, ORDER_STATUS_ORDER_SOLD].includes(props.order.status)}>
          {() => <InfoCard
            title={
              <b style={{fontSize: 22}}>הזמנתך בוצעה בהצלחה</b>
            }
            icon={ErrorOutlineIcon}
            toolTip="הזמנתך בוצעה בהצלחה"
            style={props.style}
            defaultExpanded={true}
          >
            הסתיים תהליך ההזמנה עבור מודעה מספר
            <span style={{
              color: '#d47474',
              fontWeight: 700,
              fontSize: 18
            }}> {props.order.orderIdentifier} </span>
            <br/>
            אנו מודים לך על רכישתך באתר דיל אנד גו ומקווים לראותך בעתיד מבצע/ת רכישות/מכירות נוספות.
            <br/>
            תהנו מהמוצר.
          </InfoCard>}
        </Case>
        <Case condition={[ORDER_STATUS_ORDER_DISPUTE, ORDER_STATUS_DISAPPROVED].includes(props.order.status)}>
          {() => <InfoCard
            title={
              <b style={{fontSize: 22}}>פניה לתמיכה</b>
            }
            icon={ErrorOutlineIcon}
            toolTip="הזמנתך בוצעה בהצלחה"
            style={props.style}
            defaultExpanded={true}
          >
            <br/>
            אנו מצטערים לשמוע כי נתקלת בבעיה בזמן רכישת המוצר.
            <br/>
            אנא פנה לשירות הלקוחות שלנו דרך
            דף
            {' '}
            <a
              style={{display: "inline", padding: 0, color: "#F28F17"}}
              target="_blank"
              href={linksMap.contactUs}>
              צור קשר
            </a>
            {' '}
            או
            {' '}
            <span
              style={{display: "inline", padding: 0, color: "#F28F17", cursor: "pointer"}}
              onClick={() => openCrispChat()}
            >
              בצעט של האתר.
            </span>
            {' '}
            או בטלפון –
            {" "}<a href={`tel:+972${linksMap.phoneNumber.replace(/-/g, "")}`}>{linksMap.phoneNumber}</a>{' '}
            <br/>
            במקביל, אנו ננסה ליצור איתך קשר בזמן הקרוב על מנת לטפל בבעיה זו.
          </InfoCard>}
        </Case>
      </Switch>
    }</When>
    <When condition={props.isProductOwner}>{() =>
      <Switch>
        <Case
          condition={[PRODUCT_STATUS_ORDER_APPROVED_WAITING_TRANSFER_APPROVAL_BY_BUYER, PRODUCT_STATUS_ORDER_APPROVED_TRANSFERRED, PRODUCT_STATUS_ORDER_APPROVED_WAITING_PAYMENT, PRODUCT_STATUS_ORDER_DISPUTE].includes(props.order.status)}>
          {() => <InfoCard
            title={
              <b style={{fontSize: 22}}>המסמכים נשלחו בהצלחה</b>
            }
            icon={ErrorOutlineIcon}
            toolTip="המסכים נשלחו בהצלחה"
            defaultExpanded={true}
          >
            המסמכים שהעלית נשלחו בהצלחה לקונה וכעת אנו מחכים לאישורו.
            <p/>
            <p>
              <LinkBlockContainer
                style={{display: "inline", padding: 0, color: "#F28F17"}}
                to={linksMap.dashboard}>
                לאיזור האישי
              </LinkBlockContainer>
            </p>
          </InfoCard>}
        </Case>
      </Switch>
    }</When>

  </>
);

ThankYouCard.defaultProps = {
  order: {},
};

export default ThankYouCard;
