import {connect} from 'react-redux'

import {adPageUrlSelector, adProductSelector, isProductOwnerSelector} from 'data/redux/selectors/productSelector';
import {orderSelector} from 'data/redux/selectors/orderSelector';
import OwnershipTransferSteps
  from 'gui/pages/mainRouter/pages/ad/components/commons/thirdStage/components/ownershipTransferSteps';

const mapStateToProps = (state, ownProps) => {
  const product = adProductSelector(state);
  const {id, ...restParsedUrl}: any = adPageUrlSelector(state);
  return {
    product: product,
    order: orderSelector(product)(state),
    isProductOwner: isProductOwnerSelector(state),
  };
};

const mapDispatchToProps = (dispatch) => {
  return {};
};

const OwnershipTransferStepsContainer = connect(mapStateToProps, mapDispatchToProps)(OwnershipTransferSteps);

export default OwnershipTransferStepsContainer;
