import React from "react";
import styles from './ReportProblemWithDocumentsFromSellerButton.module.scss';
import {Button} from "@material-ui/core";
import {IOrder} from 'data/interfaces/orders';

export interface IReportProblemWithDocumentsFromSellerButtonProps {
  order: IOrder
  disapprove?: (product_id: number) => void;
}

const ReportProblemWithDocumentsFromSellerButton = (props: IReportProblemWithDocumentsFromSellerButtonProps) => <Button
  variant="contained"
  className={styles.ReportProblemWithDocuments}
  onClick={() => {
    return props.disapprove(props.order.id);
  }}
>דווח על בעיה</Button>;

ReportProblemWithDocumentsFromSellerButton.defaultProps = {
  product: {},
};

export default ReportProblemWithDocumentsFromSellerButton;
