import React from 'react';
import styles from './OwnershipTransferBuyerAcceptance.module.scss';

import {IOrder} from 'data/interfaces/orders';
import {IProduct} from 'data/interfaces/products';
import InfoCard from 'gui/pages/mainRouter/pages/ad/components/commons/infoCard/InfoCard';
import CloudUploadRoundedIcon from '@material-ui/icons/CloudUploadRounded';
import {When} from 'react-if';
import {
  ORDER_STATUS_APPROVED_TRANSFER,
  ORDER_STATUS_ORDER_APPROVED_WAITING_TRANSFER_APPROVAL_BY_BUYER
} from 'data/redux/constants/ordersConstants';
import UploadedDocumentsContainer from 'data/redux/containers/uploadedDocumentsContainer/UploadedDocumentsContainer';
import {Container, Grid} from '@material-ui/core';
import AcceptDocumentsFromSellerButtonContainer
  from 'data/redux/containers/acceptDocumentsFromSellerButtonContainer/AcceptDocumentsFromSellerButtonContainer';
import ReportProblemWithDocumentsFromSellerButtonContainer
  from 'data/redux/containers/reportProblemWithDocumentsFromSellerButtonContainer/ReportProblemWithDocumentsFromSellerButtonContainer';


interface IOwnershipTransferBuyerAcceptanceProps {
  order?: IOrder;
  product?: IProduct;
  isOrderOwner?: boolean;
}

const OwnershipTransferBuyerAcceptance = (props: IOwnershipTransferBuyerAcceptanceProps) => (
  <When
    condition={!!props.isOrderOwner && [ORDER_STATUS_APPROVED_TRANSFER, ORDER_STATUS_ORDER_APPROVED_WAITING_TRANSFER_APPROVAL_BY_BUYER].includes(props.order.status)}>{() =>
    <InfoCard
      className={styles.ownershipTransferStepContainer}
      title='מסמכים מאת המוכר(לחץ על מסמך כדי להוריד)'
      icon={CloudUploadRoundedIcon}
      toolTip='המסמכים שהמוכר העביר אלייך'
      defaultExpanded={true}
    >
      <UploadedDocumentsContainer/>
      <Container>
        <When condition={props.order.status === ORDER_STATUS_ORDER_APPROVED_WAITING_TRANSFER_APPROVAL_BY_BUYER}>
          <Grid container spacing={1}>
            <Grid item xs={12}>
              <div className={styles.acceptDenyButtons}>
                <ReportProblemWithDocumentsFromSellerButtonContainer/>
                <AcceptDocumentsFromSellerButtonContainer/>
              </div>
            </Grid>
          </Grid>
        </When>
        <When condition={props.order.status === ORDER_STATUS_APPROVED_TRANSFER}>
          <Grid container spacing={1}>
            <Grid item xs={12}>

            </Grid>
          </Grid>
        </When>
      </Container>

    </InfoCard>}
  </When>
);

OwnershipTransferBuyerAcceptance.defaultProps = {};

export default React.memo(OwnershipTransferBuyerAcceptance);
