import React from "react";
import styles from './UploadedDocuments.module.scss';
import {IProduct} from 'data/interfaces/products';
import {IOrder} from 'data/interfaces/orders';
import UploadDocumentContainer from 'data/redux/containers/uploadDocumentContainer';
import {Container, Grid} from "@material-ui/core";

export interface IPassengersProps {
  order: IOrder
  product: IProduct;

  onFilesAdd: (product_id: number, files: File[]) => void;
  onFileDelete: (product_id: number, file: File) => void;
  loadInitialFiles: (product_id: number) => void;
  initialFiles: any;
}

const UploadedDocuments = (props: IPassengersProps) => {
  React.useEffect(() => {
    props.loadInitialFiles(props.product.id);
  }, [props.product.id, props.loadInitialFiles]);

  return (
    <Container className={styles.uploadedDocumentsContainer}>
      <Grid container spacing={1}>
        {props
          .initialFiles.map((file, key) => (
            <Grid item xs={3} key={file.uploadedFile.id || ""}>
              <UploadDocumentContainer fileObject={{file}}/>
            </Grid>
          ))}
      </Grid>
    </Container>
  );
};

UploadedDocuments.defaultProps =
  {
    product: {}
    ,
    order: {}
    ,
    onFilesAdd: (product_id, files) => {
      
    },

    onFileDelete: (product_id, file) => {
      
    },
  };

export default React.memo(UploadedDocuments);
