import React from "react";
import styles from './SendDocumentsToBuyerButton.module.scss';
import {IProduct} from 'data/interfaces/products';
import {Button} from "@material-ui/core";
import ScrollToSelectorOnClick from 'gui/components/scrollToSelectorOnClick';

export interface IApproveButton {
  product: IProduct;
  approve: (product_id: number) => void;
  filesAdded: boolean;
  bankValid: boolean;
  additionalDisabled?: boolean;
}

const SendDocumentsToBuyerButton = (props: IApproveButton) =>
  <ScrollToSelectorOnClick
    selector=".MuiCollapse-entered"
    topOffset={230}
  >
    <Button
      variant="contained"
      className={styles.sendDocumentsToBuyerButton}
      disabled={!props.filesAdded || !props.bankValid || props.additionalDisabled}
      onClick={() => {
        return props.approve(props.product.id);
      }}
    >שלח מסמכים לקונה</Button>
  </ScrollToSelectorOnClick>;

SendDocumentsToBuyerButton.defaultProps = {
  product: {},
  additionalDisabled: false,
};

export default SendDocumentsToBuyerButton;
