import React from "react";
import {IOrder} from 'data/interfaces/orders';
import Passenger from "./components/passenger/Passenger";
import Step
  from 'gui/pages/mainRouter/pages/ad/components/commons/thirdStage/components/ownershipTransferSteps/components/step';
import AssignmentRoundedIcon from '@material-ui/icons/AssignmentRounded';
import EmailRoundedIcon from '@material-ui/icons/EmailRounded';
import {Fab} from '@material-ui/core';
import {saveAs} from 'file-saver';
import {IProduct} from 'data/interfaces/products';
import {generateUTF8CSVString} from 'data/utils/shortcuts';
import moment from 'moment';
import Mailto from 'react-mailto.js';
import {
  hotelDetails,
  identifyProductCategoryType,
  ticketDetails,
  vacationDetails,
  voucherDetails
} from 'data/redux/constants/productsConstants';


export interface IPassengersProps {
  order: IOrder
  product: IProduct;
  onStateChange?: (complete: boolean) => void;
}

const labelToKey = {
  'First name': 'firstName',
  'Last name': 'lastName',
  'Birth date': 'birthDate',
};

const customObjectProcessor = passenger => {
  if (typeof passenger.birthDate === 'number') {
    return {...passenger, birthDate: moment(passenger.birthDate).format('DD.MM.YYYY')};
  }
  return passenger;
}
/**
 * Generate string for mailto with passengers
 * @param props
 */
const generateEmailString = props => {
  const result = [];
  const passengers = props.order.passengerDetails.map(customObjectProcessor);
  for (const passenger of passengers) {
    result.push("---------------")
    const row = [];
    result.push(`Passenger ${passengers.indexOf(passenger) + 1}:`);
    for (const label in labelToKey) {
      if (passenger[labelToKey[label]]) {
        row.push([`${label}:`, passenger[labelToKey[label]]].join("\n"));
      }
    }
    result.push(row.join("\n").trim());
  }
  result.push("---------------")
  return result.join("\n");
}
/**
 * Generate CSV string with utf8 support
 * @param props
 */
const generateCSVString = props => generateUTF8CSVString(props.order.passengerDetails,
  labelToKey,
  customObjectProcessor
);

const savePassengersAsCsv = props => saveAs(new Blob([`${generateCSVString(props)}`],), `passengers-${props.product.productIdentifier}.csv`);

const stepText = (props: IPassengersProps) => {
  switch (identifyProductCategoryType(props.product)) {
    case ticketDetails:
      return 'לפניך שמות הרוכשים. יש לשנות מול ספק התיירות או האתר שממנו רכשת את כרטיסיך לשמות הנוסעים החדשים';
    case hotelDetails:
      return '';
    case vacationDetails:
      return '';
    case voucherDetails:
      return 'לפניך שמות הרוכשים. אם יש צורך לשנות שם בשובר, אנא עשה זאת.';
  }
  return (null);
}


const Passengers = (props: IPassengersProps) => {
  return (
    <Step index={1}
          title={stepText(props)}
          onStateChange={props.onStateChange}>
      <div
        style={{
          borderStyle: 'dashed',
          padding: 12
        }}
      >
        {props.order.passengerDetails.map((passenger, index) =>
          <Passenger
            key={index}
            ticketIndex={index + 1}
            passenger={passenger}/>)}
      </div>
      <h5>אנא וודה שפרטי השמות מועברים תקין עם האיות מדוייק על מנת להמנע מחיובים נוספים*</h5>
      <p>לנוחיותך כפתור הורדת מסמך וגם כפתור שליחת מייל עם כל הפרטים הרלוונטיים, כדי למנוע טעויות בהעתקה:</p>
      <div style={{marginBottom: 15}}>
        <Fab
          aria-label="document"
          variant="extended"
          onClick={() => savePassengersAsCsv(props)}>
          <AssignmentRoundedIcon/>
          שמור בתור מסמך
        </Fab>

        <Mailto
          secure={true}
          to=""
          subject={`Change ticket ownership for product ${props.product.productIdentifier}`}
          body={[
            'New ticket owner details:',
            generateEmailString(props),
            '\n',
            'More details can be found at:',
            `https://dealandgo.co.il/ad/${props.product.id}`
          ].join('\n')}
        >
          <Fab aria-label="email" variant="extended">
            <EmailRoundedIcon/>
            שלח במייל לספק התיירות
          </Fab>
        </Mailto>
      </div>
    </Step>
  );
};

Passengers.defaultProps = {
  order: {passengerDetails: []},
  onStateChange: (complete) => {
  },
};

export default React.memo(Passengers);
