import React from "react";
import styles from './AcceptDocumentsFromSellerButton.module.scss';
import {Button} from "@material-ui/core";
import {IOrder} from 'data/interfaces/orders';

export interface IApproveButton {
  order: IOrder
  approve: (product_id: number) => void;
}

const AcceptDocumentsFromSellerButton = (props: IApproveButton) => <Button
  variant="contained"
  className={styles.acceptDocumentsFromSellerButton}
  onClick={() => {
    return props.approve(props.order.id);
  }}
>אשר קבלת מסמכים תקינים</Button>;

AcceptDocumentsFromSellerButton.defaultProps = {
  product: {},
};

export default AcceptDocumentsFromSellerButton;
