import React, {useState} from 'react';
import styles from './OwnershipTransferSteps.module.scss';
import {IOrder} from 'data/interfaces/orders';
import {IProduct} from 'data/interfaces/products';
import InfoCard from 'gui/pages/mainRouter/pages/ad/components/commons/infoCard/InfoCard';
import CloudUploadRoundedIcon from '@material-ui/icons/CloudUploadRounded';
import Passengers from './components/passengers';
import {When} from 'react-if';
import {
  hotelDetails,
  identifyProductCategoryType,
  PRODUCT_STATUS_ORDER_APPROVED_WAITING_TRANSFER,
  ticketDetails,
  vacationDetails,
  voucherDetails
} from 'data/redux/constants/productsConstants';
import UploadDocumentsContainer from 'data/redux/containers/uploadDocumentsContainer/UploadDocumentsContainer';
import SendDocumentsToBuyerButtonContainer
  from 'data/redux/containers/sendDocumentsToBuyerButtonContainer/SendDocumentsToBuyerButtonContainer';
import BankDetailsContainer from 'data/redux/containers/bankDetailsContainer/BankDetailsContainer';
import IssueCard
  from 'gui/pages/mainRouter/pages/ad/components/commons/thirdStage/components/ownershipTransferSteps/components/issueCard';


interface IOwnershipTransferStepsProps {
  order?: IOrder;
  product?: IProduct;
  isProductOwner?: boolean;
}

const prelude = (props: IOwnershipTransferStepsProps) => {
  switch (identifyProductCategoryType(props.product)) {
    case ticketDetails:
      return 'כל מה שנשאר לעשות זה לעביר את הבעלות על הכרטיסים והתשלום בדרך אלייך';
    case hotelDetails:
      return 'כל מה שנשאר לעשות זה לעביר את הבעלות על המלון והתשלום בדרך אלייך';
    case vacationDetails:
      return 'כל מה שנשאר לעשות זה לעביר את הבעלות להעביר את הבעלות על החבילה והתשלום בדרך אלייך';
    case voucherDetails:
      return 'כעת נשאר לצרף את השובר אחרי ווידוי שהשובר אכן ניתן להעברה ושוויו זהא לזה שנכתב במודעה.';
  }
  return (null);
}


const OwnershipTransferSteps = (props: IOwnershipTransferStepsProps) => {
  const [currentActiveStates, setActiveStates] = useState({});
  return (
    <When
      condition={!!props.isProductOwner && props.product.status === PRODUCT_STATUS_ORDER_APPROVED_WAITING_TRANSFER}>{() =>
      <InfoCard
        className={styles.ownershipTransferStepContainer}
        title=" העלה מסמכים"
        // subTitleDetail={<>
        //   <span style={{color: '#878787'}}> זמן שנותר:</span>
        //   <span
        //     style={{color: '#05BEC0'}}>  {getHours(new Date().getTime(), productEndingSelector(props.product))}</span>
        // </>}
        icon={CloudUploadRoundedIcon}
        toolTip='שינוי שם הלקוח מול סוכן הנסיעות/ספק התיירות'
        iconStyle={{backgroundColor: "#F28F17"}}
        defaultExpanded={true}
      >
        <h5>
          איזה כיף יש קונה להצעה!
          <br/>
          {prelude(props)}
        </h5>
        <h6 style={{
          color: 'red',
          fontWeight: 700,
          fontSize: 25,
        }}>
          *לפניך שלושה שלבים, בסיום של כל שלב אנא לחץ על הכפתור "בוצע"
        </h6>
        <Passengers product={props.product}
                    order={props.order}
                    onStateChange={complete => setActiveStates({...currentActiveStates, passengers: complete})}/>
        <UploadDocumentsContainer
          onStateChange={complete => setActiveStates({...currentActiveStates, uploadedDocuments: complete})}/>
        <BankDetailsContainer
          onStateChange={complete => setActiveStates({...currentActiveStates, bankDetails: complete})}/>
        <IssueCard/>
        <SendDocumentsToBuyerButtonContainer
          additionalDisabled={Object.values(currentActiveStates).filter(s => s === true).length < 3}/>
      </InfoCard>
      }
    </When>
  )
};

OwnershipTransferSteps.defaultProps = {};

export default React.memo(OwnershipTransferSteps);
