import {connect} from 'react-redux'

import {adPageUrlSelector, adProductSelector} from 'data/redux/selectors/productSelector';
import {isProductOrderOwnerSelector, orderSelector} from 'data/redux/selectors/orderSelector';
import OwnershipTransferBuyerAcceptance
  from 'gui/pages/mainRouter/pages/ad/components/commons/thirdStage/components/ownershipTransferBuyerAcceptance/OwnershipTransferBuyerAcceptance';

const mapStateToProps = (state, ownProps) => {
  const product = adProductSelector(state);
  const {id, ...restParsedUrl}: any = adPageUrlSelector(state);
  return {
    product: product,
    order: orderSelector(product)(state),
    isOrderOwner: isProductOrderOwnerSelector(product)(state),
  };
};

const mapDispatchToProps = (dispatch) => {
  return {};
};

const OwnershipTransferBuyerAcceptanceContainer = connect(mapStateToProps, mapDispatchToProps)(OwnershipTransferBuyerAcceptance);

export default OwnershipTransferBuyerAcceptanceContainer;
