import {connect} from 'react-redux'
import {isProductValidBankSelector} from 'data/redux/selectors/productSelector';
import BankDetails
  from 'gui/pages/mainRouter/pages/ad/components/commons/thirdStage/components/ownershipTransferSteps/components/bankDetails/BankDetails';


const mapStateToProps = (state, ownProps) => {
  return {
    bankValid: isProductValidBankSelector(state),
    ...ownProps,
  };
};


const mapDispatchToProps = (dispatch, ownProps) => {
  return {
    ...ownProps
  };
};


const BankDetailsContainer = connect(mapStateToProps, mapDispatchToProps)(BankDetails);

export default BankDetailsContainer;
