import React from 'react';
import styles from './BankDetailsForm.module.scss';
import {FastField, Form, Formik} from 'formik';
import * as Yup from "yup";
import {Button, Grid} from '@material-ui/core';
import objectArrayTools from 'object-arrarify';
import AutoCompleteBankSelectorContainer
  from 'data/redux/containers/autoCompleteBankSelectorContainer/AutoCompleteBankSelectorContainer';
import AutoCompleteBranchSelectorContainer from 'data/redux/containers/autoCompleteBranchSelectorContainer';
import AccountInput from './components/accountInput';
import {Else, If, Then} from 'react-if';
import {RESULT, validateBankAccount} from 'israeli-bank-validation';
import {IBankDetails, IProduct} from 'data/interfaces/products';
import FormikTextInput from 'gui/components/formikTextInput';


interface IBankDetailsFormProps {
  onSubmit: (formValues) => Promise<any>;
  product: IProduct;
  bankDetails?: IBankDetails;
  setSubmitRef?: any;
}

export const bankDetailsYupSchemaObject = Yup.object({
  bank: Yup.object({
    bankName: Yup.string().min(1).max(1000).required("יש לבחור בנק"),
    bankCode: Yup.number().min(1).max(999).required("יש לבחור בנק"),
  })
    .typeError('יש לבחור בנק')
    .required("יש לבחור בנק")
    .noUnknown(true),
  branch: Yup.object({
    bankName: Yup.string().min(1).max(1000).required("יש לבחור סניף"),
    bankCode: Yup.number().min(1).max(999).required("יש לבחור סניף"),
    branchCode: Yup.string().min(1).max(1000).required("יש לבחור סניף"),
    branchName: Yup.string().min(1).max(1000).required("יש לבחור סניף"),
    branchAddress: Yup.string().min(1).max(1000).required("יש לבחור סניף"),

  })
    .typeError('יש לבחור סניף')
    .required("יש לבחור סניף")
    .noUnknown(true),
  account: Yup.number().min(1111, "מספר חשבון קצר מדיי").max(9999999999, "מספר חשבון ארוך מדיי").required("יש לרשום מספר חשבון")
    .when(['bank', 'branch'], (bank, branch, schema) => schema.test({
      test: account => {
        const bankCode = `${branch && branch.bankCode || 0}`;
        const branchCode = `${branch && branch.branchCode || 0}`;
        return validateBankAccount(bankCode, branchCode, `${account || 0}`) === RESULT.VALID;
      },
      message: "מספר חשבון לא תקין, אינו תואם למספר בנק וסניף"
    }))
  ,
  owner: Yup.string().min(0).max(100).required("צריך לרשום שם מלא של בעל החשבון"),
}).noUnknown(true);
export const bankDetailsInitialValues = {
  bank: null,
  branch: null,
  account: "",
  owner: "",
};

const valuesToBankDetails = values => {
  try {
    return {
      bank: parseInt(values.bank.bankCode),
      branch: parseInt(values.branch.branchCode),
      account: parseInt(values.account as any),
      owner: values.owner,
    };
  } catch (e) {
  }
  return {};
}

const BankDetailsForm = (props: IBankDetailsFormProps) => {
  const bankDetails = {
    ...bankDetailsInitialValues,
    ...props.bankDetails
  };

  return (
    <Formik
      initialValues={bankDetails}
      validationSchema={bankDetailsYupSchemaObject}
      onSubmit={async (values, {setSubmitting}) => {
        setSubmitting(false);
        await props
          .onSubmit({
            id: props.product.id,
            bankDetails: valuesToBankDetails(values),
          });
      }}
      initialTouched={objectArrayTools(props.bankDetails).map(() => true).toNormalObject()}
      enableReinitialize={true}
    >
      {({submitForm, isSubmitting, setFieldValue, values, touched, errors, submitCount}) => (
        <Form translate={null}>
          <Grid className={styles.BankDetailsForm}
                container
                spacing={2}>
            <Grid item xs={12} sm={4}>
              <FastField
                component={AutoCompleteBankSelectorContainer}
                label="בנק *"
                name="bank"/>
            </Grid>
            <Grid item xs={12} sm={4}>
              <FastField
                component={AutoCompleteBranchSelectorContainer}
                label="סניף *"
                name="branch"/>
            </Grid>
            <Grid item xs={12} sm={4}>
              <AccountInput
                label="מספר חשבון"
                name="account"
              />
            </Grid>
            <Grid item style={{marginTop: 15}} xs={12}>
              <FormikTextInput
                label="שם בעל החשבון"
                name="owner"
                autoComplete="fname"
                values={values}
                touched={touched}
                errors={errors}
              />
            </Grid>

            <Grid item xs={12}>
              <If condition={!!(props.bankDetails && props.bankDetails.bank)}>
                <Then>
                  <Button
                    className={`${styles.nextButton} ${styles.updateButton}`}
                    variant="contained"
                    color="primary"
                    disabled={isSubmitting || JSON.stringify(valuesToBankDetails(bankDetails)) === JSON.stringify(valuesToBankDetails(values))}
                    onClick={submitForm}
                    ref={props.setSubmitRef}
                  >
                    עדכן פרטי בנק
                  </Button>
                </Then>
                <Else>
                  <Button
                    className={styles.nextButton}
                    variant="contained"
                    color="primary"
                    disabled={isSubmitting}
                    onClick={submitForm}
                    ref={props.setSubmitRef}
                  >
                    שמור פרטי בנק
                  </Button>

                </Else>
              </If>
            </Grid>

          </Grid>
        </Form>
      )}
    </Formik>
  );
};

BankDetailsForm.defaultProps = {
  product: {id: 0},
  onSubmit: (formValues) => new Promise((resolve) => {
    
    setTimeout(resolve, 1000);
  }),
  ticketDetails: {},
  setSubmitRef: () => {
  },
};

export default React.memo(BankDetailsForm);
