import React from "react";
import styles from './Passenger.module.scss';
import {MDBCol, MDBRow} from 'mdbreact';

import moment from 'moment';
import {IPassenger} from 'data/interfaces/orders';
import {When} from 'react-if';
import CopyTextToClipBoardButton from 'gui/components/copyTextToClipBoardButton';

export interface IPassengerProps {
  ticketIndex: number;
  passenger: IPassenger

}


export const passengerToCsvLine = (passenger: IPassenger) => [passenger.firstName, passenger.lastName, passenger.birthDate > 0 && moment(passenger.birthDate).format('DD.MM.YYYY')].filter(s => s).join(",");

export const passengersToCsv = (...passengers: IPassenger[]) => `${[
  (['שם פרטי', "שם משפחה", (passengers.length > 0 && passengers[0].birthDate > 0) ? 'תאריך לידה' : null].filter(s => s)).reverse(),
  ...passengers.map(passengerToCsvLine)].join("\n")
}`;


const Passenger = (props: IPassengerProps) => {
  return (
    <>
      <MDBRow>
        <MDBCol sm="12">
          <h5 className={styles.passengerNumberLabel}>
            נוסע מספר {props.ticketIndex}:
          </h5>
        </MDBCol>
      </MDBRow>

      <MDBRow>
        <MDBCol sm="4">
          <h6 className={styles.passengerDetailLabel}>שם פרטי</h6>
          <p className={styles.passengerDetailValue}>
            <CopyTextToClipBoardButton text={props.passenger.firstName} showCopyContent/>
          </p>
        </MDBCol>
        <MDBCol sm="4">
          <h6 className={styles.passengerDetailLabel}>שם משפחה</h6>
          <p className={styles.passengerDetailValue}>
            <CopyTextToClipBoardButton text={props.passenger.lastName} showCopyContent/>
          </p>
        </MDBCol>
        <When condition={!!props.passenger.birthDate && props.passenger.birthDate > 0}>
          <MDBCol sm="4">
            <h6 className={styles.passengerDetailLabel}>תאריך לידה</h6>
            <p className={styles.passengerDetailValue}>
              <CopyTextToClipBoardButton text={moment(props.passenger.birthDate).format('DD.MM.YYYY')} showCopyContent/>
            </p>
          </MDBCol>
        </When>
      </MDBRow>
      <MDBRow style={{marginTop: 10, marginBottom: 10}}/>
    </>
  );
};

Passenger.defaultProps = {
  ticketIndex: 1,
  passenger: {},
};

export default React.memo(Passenger);
